<template>
    <div>
        <h3 class="logo">小U商城</h3>
        <el-menu
      :collapse="iscollapse"  
      default-active="2"
      background-color="#545c64"
      text-color="#fff"
      router
    >
      <el-menu-item index='/'  >
        <i class="el-icon-lollipop"></i>后台首页
      </el-menu-item>
      <el-submenu :index="'top'+index" v-for="(item,index) in menus" :key="index">
        <template slot="title">
          <i :class="item.icon"></i>
          <span>{{item.title}}</span>
        </template>
        <template v-if="item.children && item.children.length">
          <el-menu-item v-for="(val,idx) in item.children" :key='idx'  :index="val.url">{{val.title}}</el-menu-item>
        </template>
      </el-submenu>
    </el-menu>
    </div>
</template>

<script>
import { mapState,mapGetters } from 'vuex';

export default {
  computed: {
    ...mapState([ 'iscollapse']),
    ...mapGetters(['menus'])
  },
};
</script>
<style scoped>
.logo{
    line-height: 60px;
    text-align: center;
    font-size: 24px;
    font-weight: normal;
    white-space: nowrap;
    overflow: hidden;
    background-color: #383b3e;
    color: #fff;
}
.el-menu{
  border-right: none;
}
.el-menu-item.is-active{
  background-color: #FB6D49!important;
  color: #fff;
}
</style>


