<template>
   <el-container class="page-box">
     <el-aside  :class="iscollapse ? 'page-nav hide':'page-nav'">
       <Nav/>
     </el-aside>
     <el-container class="page-main">
       <el-header class="page-header" >
          <Header/>
       </el-header>
       <el-main  class="page-content">
         <router-view/>
       </el-main>
     </el-container>
   </el-container>
   
</template>

<script>
import { mapState } from "vuex"
import Header from "./Header"
import Nav from "./Nav"
export default {
    data(){
        return{}
    },
    created(){},
    computed: {
       ...mapState(['iscollapse']),
    },
    components:{
      Header,Nav
    }
}

</script>
<style scoped>
.page-box{
  width: 100vw;
  height: 100vh;
}
.page-nav{
  width: 230px!important;
  background-color: #545c64;
}
.page-nav.hide{
  width: 64px!important;
}
.page-main{
  background-color: #f1f1f1;
}
.page-header{
  height: 60px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .2);
  background-color: #fff;
  z-index: 10;
}
</style>


