<template>
  <div  class="header">
    <div class="lt">
      <el-button @click="TOGGLE" type="primary" icon="el-icon-s-fold" size="mini"></el-button>&nbsp;&nbsp;&nbsp;&nbsp;
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item v-if="$route.name!='index'">{{$route.meta.title}}</el-breadcrumb-item>
      </el-breadcrumb>&nbsp;&nbsp;&nbsp;&nbsp;
      <el-button  v-if="$route.name!='index'" icon="el-icon-back" circle size="mini" @click="$router.go(-1)"></el-button>
    </div>
    <el-dropdown>
      <span class="el-dropdown-link">
        {{username}}
        <i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>
          <i class="el-icon-full-screen"></i>全屏操作
        </el-dropdown-item>
        <el-dropdown-item>
          <i class="el-icon-user"></i>个人信息
        </el-dropdown-item>
        <el-dropdown-item @click.native="quit">
          <i class="el-icon-switch-button"></i>退出
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapMutations,mapGetters,mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters(['username'])
  },
  methods: {
    ...mapMutations(['TOGGLE']),
    ...mapActions({
      quit:"user/quit"
    })
  },
};
</script>
<style scoped>
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
.header .lt{
  display: flex;
  align-items: center;
}
</style>


